/* src/App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: linear-gradient(180deg, #007bff, #0056b3), url('https://www.google.com/url?sa=i&url=https%3A%2F%2Fwww.freepik.com%2Ffree-photos-vectors%2Fcomic-background&psig=AOvVaw3OW3dyhlsT5B_E-rBxWHKD&ust=1700506695882000&source=images&cd=vfe&opi=89978449&ved=0CBIQjRxqFwoTCOjk4p7f0IIDFQAAAAAdAAAAABAE'); /* Replace 'your-image-url.jpg' with your image URL */
  background-size: cover;
  background-position: center;
  color: #fff;
  display: flex;
  flex-direction: column;
}

header {
  background-color: rgba(0, 123, 255, 0.9); /* Adjust the alpha (fourth) value for transparency */
  padding: 15px;
  text-align: center;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h1 {
  margin: 0;
}

main {
  width: 100%;
  max-width: 600px;
  padding: 20px;
  box-sizing: border-box;
  flex-grow: 1; /* Expand to fill available space */
}

textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
}

button {
  padding: 10px;
  background-color: rgba(0, 86, 179, 0.9); /* Adjust the alpha (fourth) value for transparency */
  color: #fff;
  border: none;
  cursor: pointer;
  width: 100%;
}

button:hover {
  background-color: rgba(0, 64, 128, 0.9); /* Adjust the alpha (fourth) value for transparency */
}

.error-message {
  color: #ff0000;
  margin-top: 10px;
}

img {
  max-width: 100%;
  height: auto;
  margin-top: 10px;
}

/* Footer adjustments */
footer {
  margin-top: auto;
  padding: 10px;
  background-color: rgba(0, 64, 128, 0.9); /* Adjust the alpha (fourth) value for transparency */
  color: #fff;
  text-align: center;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
